                  <el-date-picker
                          v-model="emp.begincontract"
                          size="mini"
                          type="date"
                          style="width: 130px;"
                          value-format="yyyy-MM-dd"
                          placeholder="合同起始日期"
                  >
                  </el-date-picker>
                </el-form-item>
              </el-col>
              <el-col :span="6">
                <el-form-item label="合同中止日期:" prop="endcontract">
                  <el-date-picker
                          v-model="emp.endcontract"
                          size="mini"
                          type="date"
                          style="width: 130px;"
                          value-format="yyyy-MM-dd"
                          placeholder="合同中止日期"
                  >
                  </el-date-picker>
                </el-form-item>
              </el-col>
            </el-row>
            <el-row>
              <el-col :span="8">
                <el-form-item label="身份证号码:" prop="idcard">
                  <el-input
                          size="mini"
                          style="width: 150px;"
                          prefix-icon="el-icon-edit"
                          v-model="emp.idcard"
                          placeholder="身份证号码"
                  ></el-input>
                </el-form-item>
              </el-col>
              <el-col :span="8">
                <el-form-item label="聘用形式:" prop="engageform">
                  <el-radio-group v-model="emp.engageform">
                    <el-radio label="劳动合同" style="margin-right: 10px"
                    >劳动合同</el-radio
                    >
                    <el-radio label="劳务合同">劳务合同</el-radio>
                  </el-radio-group>
                </el-form-item>
              </el-col>
              <el-col :span="8">
                <el-form-item label="婚姻状况:" prop="wedlock">
                  <el-radio-group v-model="emp.wedlock">
                    <el-radio label="已婚" style="margin-right: 10px"
                    >已婚</el-radio
                    >
                    <el-radio label="未婚" style="margin-right: 10px"
                    >未婚</el-radio
                    >
                    <el-radio label="离异">离异</el-radio>
                  </el-radio-group>
                </el-form-item>
              </el-col>
            </el-row>
          </el-form>
