        <span slot="footer" class="dialog-footer">
          <el-button @click="dialogVisible = false">取 消</el-button>
          <el-button type="primary" @click="doAddEmp"
            >确 定</el-button
          >
        </span>
      </el-dialog>
    </div>
  </div>
</template>

<script>
export default {
  name: "EmpBasic",
  data() {
    return {
        searchValue:{
            politicid:1,
            nationid:1,
            joblevelid:1,
            posid:1,
            engageform:"劳务合同",
            departmentid: 1,
            beginDateScope:null,
        },
        showAdvanceSearchView:false,
        title:'',
        allDeps:[],
        popvisible:false,
        options: ['本科','大专','硕士','高中','小学','初中','博士','其他'],
        value: '',
      dialogVisible: false,
      name: "",
      emps: [],
