      total: 0,
      page: 1,
      size: 10,
        nations:[],
        joblevels:[],
        politicsstatus:[],
        positions:[],
      emp: {
        name: "123456789",
        gender: "男",
        birthday: "1989-12-31",
        idcard: "610122199001011252",
        wedlock: "已婚",
        nationid: 1,
        nativeplace: "陕西",
        politicid: 13,
        email: "laowang@qq.com",
        phone: "18565558897",
        address: "深圳市南山区",
        departmentid: 5,
        joblevelid: 9,
        posid: 29,
        engageform: "劳务合同",
        tiptopdegree: "本科",
        specialty: "信息管理与信息系统",
        school: "深圳大学",
        begindate: "2017-12-31",
        workstate: "在职",
        workid: "12000001",
        contractterm: 2.0,
        conversiontime: "2018-03-31",
        notworkdate: null,
        begincontract: "2017-12-31",
