            idcard: "",
            wedlock: "",
            nationid: 1,
            nativeplace: "",
            politicid: 13,
            email: "",
            phone: "",
            address: "",
            departmentid: 5,
            joblevelid: 9,
            posid: 29,
            engageform: "",
            tiptopdegree: "",
            specialty: "",
            school: "",
            begindate: "",
            workstate: "在职",
            workid: "",
            contractterm: 2.0,
            conversiontime: "",
            notworkdate: null,
            begincontract: "",
            endcontract: "",
            workage: null
        }
      },
      showEditEmpView(data){
          this.title='编辑员工信息';
          this.dialogVisible =true;
          this.emp = data;
      },
      deleteEmp(data){
          this.$confirm('此操作将永久删除该文件, 是否继续?', '提示', {
              confirmButtonText: '确定',
              cancelButtonText: '取消',
              type: 'warning'
          }).then(() => {
              this.deleteRequest("/emp/basic/"+data.id).then(resp=>{
                  this.initAllEmps();
              })
          }).catch(() => {
              this.$message({
                  type: 'info',
                  message: '已取消删除'
              });
          });
      },
      doAddEmp(){
          if(this.emp.id){
              console.log(this.emp.id);
              this.$refs['empForm'].validate(valid=>{
                  if(valid){
                      this.putRequest("/emp/basic/",this.emp).then(resp=>{
                          if(resp){
                              this.dialogVisible=false;
                              this.initAllEmps();
                          }
                      })
                  }
              })
          }else {
              this.$refs['empForm'].validate(valid=>{
                  if(valid){
                      this.postRequest("/emp/basic/",this.emp).then(resp=>{
                          if(resp){
                              this.dialogVisible=false;
                              this.initAllEmps();
                          }
                      })
                  }
              })
          }


      },
      handleNodeClick(data){
          this.popvisible = !this.popvisible;
          this.inputDepName =data.name;
          this.emp.departmentid = data.id;
      },
      showDeptView(){
      this.popvisible = !this.popvisible;

      },
      initPositions(){
          this.getRequest("/emp/basic/positions").then(resp=>{
              if(resp){
                  this.positions=resp;
              }
          })
      },
      getMaxWorkId(){
        this.getRequest("/emp/basic/maxWorkId").then(resp=>{
            if(resp){
                this.emp.workid=resp.obj;
            }

        })
      },
      initData(){
        if(!window.sessionStorage.getItem("nations")){
            this.getRequest("/emp/basic/nations").then(resp=>{
                if(resp){
                    this.nations=resp;
                    window.sessionStorage.setItem("nations",JSON.stringify(resp));
                }
            })
        }else {
            this.nations =JSON.parse(window.sessionStorage.getItem("nations"))
        }
          if(!window.sessionStorage.getItem("joblevels")){
              this.getRequest("/emp/basic/joblevels").then(resp=>{
                  if(resp){
                      this.joblevels=resp;
                      window.sessionStorage.setItem("joblevels",JSON.stringify(resp));
                  }
              })
          }else {
              this.joblevels =JSON.parse(window.sessionStorage.getItem("joblevels"))
          }
          if(!window.sessionStorage.getItem("politicsstatus")){
              this.getRequest("/emp/basic/politicsstatus").then(resp=>{
                  if(resp){
                      this.politicsstatus=resp;
                      window.sessionStorage.setItem("politicsstatus",JSON.stringify(resp));
                  }
              })
          }else {
              this.politicsstatus =JSON.parse(window.sessionStorage.getItem("politicsstatus"))
          }
          if(!window.sessionStorage.getItem("deps")){
              this.getRequest("/emp/basic/deps").then(resp=>{
                  if(resp){
                      this.allDeps=resp;
                      window.sessionStorage.setItem("deps",JSON.stringify(resp));
                  }
              })
          }else {
              this.allDeps =JSON.parse(window.sessionStorage.getItem("deps"))
          }
      },
    showAddEmpView() {
          this.title='添加员工';
          this.emptyEmp();
      this.dialogVisible = true;
      this.getMaxWorkId();
    },
    sizeChange(pageSize) {
      this.size = pageSize;
      this.initAllEmps();
    },
    currentChange(currentPage) {
      this.page = currentPage;
      console.log(this.page);
      this.initAllEmps();
    },
    initAllEmps(type) {
      this.loading = true;
      let url ='/emp/basic/?page='+
          this.page +
          "&size=" +
          this.size;
      if(type && type=='advanced'){
        url+='&politicid='+this.searchValue.politicid+'&nationid='+this.searchValue.nationid
        +'&joblevelid='+this.searchValue.joblevelid+'&posid='+this.searchValue.posid
          +'&engageform='+this.searchValue.engageform+'&departmentid='+this.searchValue.departmentid
        +'&beginDataScope='+this.searchValue.beginDateScope;
      }else {
          url += "&name=" +
              this.name;

      }
      this.getRequest(url).then(resp => {
            this.loading = false;
            if (resp) {
                this.emps = resp.data;
                this.total = resp.total;
            }
        });

    }
  }
};
</script>

<style>
  /* 可以设置不同的进入和离开动画 */
  /* 设置持续时间和动画函数 */
  .slide-fade-enter-active {
    transition: all .5s ease;
  }
  .slide-fade-leave-active {
    transition: all .5s cubic-bezier(1.0, 0.5, 0.8, 1.0);
  }
  .slide-fade-enter, .slide-fade-leave-to
    /* .slide-fade-leave-active for below version 2.1.8 */ {
    transform: translateX(10px);
    opacity: 0;
  }
</style>
